html,
body {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
	box-sizing: border-box;
}

.loader{
  z-index: 1000000;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background:  #404D4D;
}

svg{
  position: relative;
  width: 50px;
  height: 50px;
  animation: loaderAnim 0.5s cubic-bezier(.3,.6,.8,.9) infinite forwards alternate;  

  circle{
    width: 100%;
    height: 100%;
    fill: #c0bdbd;
    transform: translate(5px, 5px);        
  }
}


@keyframes loaderAnim {
	to {
		opacity: 1;
		transform: scale(2);
	}
}

#renderCanvas {
    width: 100%;
    height: 100%;
    display: block;
    font-size: 0;
}